import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Container } from '../common/Container/Container';
import { StaticImage } from 'gatsby-plugin-image';
import { device, size } from '../../utils/layoutBreakpoints';
import { colors, opacityToHex } from '../../utils/colors';

const StyledPartners = styled.section`
  padding-top: 12px;
  text-align: center;

  @media ${device.laptop} {
    padding-top: 10px;
  }

  .slick-track {
    display: flex;
    align-items: flex-end;
    object-fit: contain;
  }
  .slick-slide {
    margin: 0 10px;
    box-sizing: border-box;
  }
`;

const Title = styled.h2`
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  letter-spacing: -0.1px;
  width: 100%;

  @media ${device.laptop} {
    text-align: left;
    font-size: 20px;
    width: auto;
  }
`;

const ClientsContent = styled.div`
  display: flex;
  align-items: center;
  flex-flow: column wrap;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  background-color: ${colors.primary.black + opacityToHex[0.05]};
  border-radius: 8px;
  padding: 16px;
  box-shadow: inset 0px 1px 40px ${colors.primary.black + opacityToHex[0.15]};
  overflow: hidden;
  max-width: 600px;
  margin: 0 auto;
  
  @media ${device.laptop} {
    max-width: unset;
    margin: unset;
  }

  & > h2,
  & > div {
    transition: all 1s cubic-bezier(0.3, 1.5, 0.66, 1);
  }

  &.hide-logos {
    & > h2,
    & > div {
      opacity: 0;
      transform: translateX(80px);
    }
  }

  & > div:nth-child(2) {
    transition-delay: 0.125s;
    img {
      max-height: 26px;
    }
  }
  & > div:nth-child(3) {
    transition-delay: 0.25s;
    img {
      max-height: 20px;
    }
  }
  & > div:nth-child(4) {

    transition-delay: 0.375s;
    img {
      max-height: 24px;
    }
  }
  & > div:nth-child(5) {
    transition-delay: 0.5s;
    img {
      max-height: 18px;
    }
  }

  @media ${device.laptop} {
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: no-wrap;
    height: 64px;
    padding: 0 6% 0 40px;

    & > div:nth-child(2) {
      img {
        max-height: 38px;
      }
    }
    & > div:nth-child(3) {
      img {
        max-height: 32px;
      }
    }
    & > div:nth-child(4) {
      img {
        max-height: 40px;
      }
    }
    & > div:nth-child(5) {
      img {
        max-height: 26px;
      }
    }
  }
`;

const ImageWrapper = styled.div`
  width: 45%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 20px 0 0 0;
  img {
    max-height: 50px;
    user-select: none;
    object-fit: contain;
  }

  @media ${device.laptop} {
    width: auto;
    padding: 0;
  }
`;

const Partners = () => {

  const [animationClass, setAnimationClass] = useState<string>('hide-logos');
  useEffect(() => {
    const timer = setTimeout(() => {
      setAnimationClass('');
    }, 100);

    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <StyledPartners>
      <Container>
        <ClientsContent className={animationClass} >
          <Title>Trusted by Software Providers Like</Title>
          <ImageWrapper>
            <StaticImage
              src="../../images/Logo/Clients/Constant-Contact.png"
              alt="Constant Contact logo"
              placeholder="none"
              objectFit="contain"
              height={38}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../images/Logo/Clients/Boulevard.png"
              alt="Boulevard logo"
              placeholder="none"
              objectFit="contain"
              height={32}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../images/Logo/Clients/Riptide.png"
              alt="Riptide logo"
              placeholder="none"
              objectFit="contain"
              height={30}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../images/Logo/Clients/Walla.png"
              alt="Walla logo"
              placeholder="none"
              objectFit="contain"
              height={26}
            ></StaticImage>
          </ImageWrapper>
        </ClientsContent>
      </Container>
    </StyledPartners>
  );
};

export default Partners;
